@tailwind base;
@tailwind components;
@tailwind utilities;

@layer utilities {
  .clip-navbar {
    clip-path: polygon(
      0.22% 15.3%,
      2% 2%,
      30.9% 2%,
      32.09% 9.48%,
      65.8% 9.48%,
      66.1% 12.1%,
      98.2% 12.1%,
      99.79% 25%,
      99.79% 25%,
      99.79% 84.8%,
      98.05% 98.05%,
      69.1% 98.05%,
      67.95% 90.5%,
      34.2% 90.5%,
      33.9% 87.9%,
      1.8% 87.9%,
      0.22% 75.4%
    );
  }
}

